@mixin theme--navy {
  .btn {
    background-color: $color-navy;
  }

  .card-category__author {
    color: $color-navy;
  }
}

@mixin theme--irish-blue {
  .btn {
    background-color: $color-iris-blue;
  }

  .card-category__author {
    color: $color-iris-blue;
  }
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-992 {
  @media (max-width: ($vp-993 - 1px)) {
    @content;
  }
}

@mixin vp-768 {
  @media (max-width: ($vp-769 - 1px)) {
    @content;
  }
}

@mixin vp-576-only {
  @media (width: $vp-576) {
    @content;
  }
}

@mixin vp-576 {
  @media (max-width: ($vp-577 - 1px)) {
    @content;
  }
}

@mixin vp-575 {
  @media (max-width: ($vp-576 - 1px)) {
    @content;
  }
}

